// src/App.js

import React, { useState } from "react"
import CodePopup from "./components/CodePopup"
import CSSPopup from "./components/CSSPopup"
import "./styles/CodePopup.scss"
import "./App.css"
import Header from "./components/Header"
import headerCode from "./components/Codes/HeaderCode"

function App() {
  const [editMode, setEditMode] = useState(false)
  const [isCodePopupOpen, setIsCodePopupOpen] = useState(false)
  const [isCssPopupOpen, setIsCssPopupOpen] = useState(false)
  const [codeContent, setCodeContent] = useState("")
  const [cssContent, setCssContent] = useState("")

  const handleMouseOver = (content) => {
    setCodeContent(content)
    setIsCodePopupOpen(true)
  }

  const handleCloseCodePopup = () => {
    setIsCodePopupOpen(false)
  }

  const handleToggleCssMode = () => {
    setIsCodePopupOpen(false)
    setIsCssPopupOpen(true)
    const currentCss = Array.from(document.styleSheets)
      .map((styleSheet) => {
        try {
          return Array.from(styleSheet.cssRules)
            .map((rule) => rule.cssText)
            .join("\n")
        } catch (e) {
          console.error(e)
          return ""
        }
      })
      .join("\n")
    setCssContent(currentCss)
  }

  const handleCloseCssPopup = () => {
    setIsCssPopupOpen(false)
  }

  // eslint-disable-next-line
  const toggleEditMode = () => {
    setEditMode(!editMode)
  }

  return (
    <div className={`App ${editMode ? "edit-mode" : ""}`}>
      <Header onMouseOver={handleMouseOver} headerCode={headerCode} />
      <main>
        <section id="about">
          <h2>Quem Sou</h2>
          <p>Breve descrio sobre voc.</p>
          {editMode && (
            <div>
              <h3>Cdigo Fonte:</h3>
              {/* Adicione aqui o cdigo fonte da seo "Quem Sou" */}
              <button>Editar CSS</button>
            </div>
          )}
        </section>
        <section id="projects">
          <h2>Projetos</h2>
          <p>Descrio dos projetos.</p>
          {editMode && (
            <div>
              <h3>Cdigo Fonte:</h3>
              {/* Adicione aqui o cdigo fonte da seo "Projetos" */}
              <button>Editar CSS</button>
            </div>
          )}
        </section>
        <section id="contact">
          <h2>Contato</h2>
          <p>Informaes de contato.</p>
          {editMode && (
            <div>
              <h3>Cdigo Fonte:</h3>
              {/* Adicione aqui o cdigo fonte da seo "Contato" */}
              <button>Editar CSS</button>
            </div>
          )}
        </section>
      </main>
      <CodePopup
        isOpen={isCodePopupOpen}
        codeContent={codeContent}
        onClose={handleCloseCodePopup}
        onToggleCssMode={handleToggleCssMode}
      />
      <CSSPopup
        isOpen={isCssPopupOpen}
        onClose={handleCloseCssPopup}
        initialCss={cssContent}
      />
    </div>
  )
}

export default App
