import React, { useEffect, useState, useRef } from "react"

const { validate } = require("css-validator")

const CSSPopup = ({ isOpen, onClose, initialCss }) => {
  const [cssContent, setCssContent] = useState(initialCss)
  const popupRef = useRef(null)

  useEffect(() => {
    setCssContent(initialCss)
  }, [initialCss])

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (popupRef.current && !popupRef.current.contains(event.target)) {
        onClose()
      }
    }

    if (isOpen) {
      document.addEventListener("mousedown", handleClickOutside)
    } else {
      document.removeEventListener("mousedown", handleClickOutside)
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside)
    }
  }, [isOpen, onClose])

  const handleSaveCss = () => {
    isValidCss(cssContent)
      .then((isValid) => {
        if (isValid) {
          applyCssChanges(cssContent)
          onClose()
        } else {
          alert("O cdigo CSS inserido  invlido!")
        }
      })
      .catch(() => alert("Erro ao validar o CSS"))
  }

  const isValidCss = (css) => {
    // Execute a validao do cdigo CSS
    return new Promise((resolve, reject) => {
      validate({ text: css }, (error, data) => {
        if (error) {
          // Se ocorrer um erro, rejeite a promessa com o erro
          console.error("Erro ao validar o CSS:", error)
          reject(error)
        } else {
          // Se a validao for bem-sucedida, verifique se h erros
          if (data.errors && data.errors.length > 0) {
            // Se houver erros, o CSS  invlido, retorne false
            console.error("Erros de validao do CSS:", data.errors)
            resolve(false)
          } else {
            // Se no houver erros, o CSS  vlido, retorne true
            console.log("CSS vlido:", css)
            resolve(true)
          }
        }
      })
    })
  }

  const applyCssChanges = (css) => {
    const styleElement = document.createElement("style")
    styleElement.setAttribute("crossorigin", "anonymous")
    styleElement.innerHTML = css
    document.head.appendChild(styleElement)
  }

  return (
    <div
      id="cssPopup"
      style={{ display: isOpen ? "block" : "none" }}
      ref={popupRef}
    >
      <textarea
        value={cssContent}
        onChange={(e) => setCssContent(e.target.value)}
        rows={10}
      />
      <button onClick={handleSaveCss}>Modificar CSS</button>
      <button onClick={onClose}>X</button>
    </div>
  )
}

export default CSSPopup
